import { ENV_ENUM, getENV } from '@ali/dingtalk-jsapi/lib/env';

/** * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * *
 * !important: 注意，注意，这里是个大坑
 * 由于容器的bug，在iframe里面的页面，没有联通jsbridge，所以iframe里面无法调用到jsapi
 * - iframe只能从「顶层」window上去拿jsapi
 * - 「当前应用」需要给we-web提供必要的jsapi，挂载到window上
 * - 如果「当前应用」被嵌套到iframe中，jsapi需要父窗口提供
 * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * */
if (/dingtalk.com/.test(document.domain)) {
  /* 设置为同域，才能从windown上读值 */
  document.domain = 'dingtalk.com';
}

if (/dingtalk.test/.test(document.domain)) {
  // 日常环境
  try {
    document.domain = 'dingtalk.test';
  } catch (err) {
    console.error('日常环境中无法设置document.domain，原因未找到！', err);
  }
}

type APICollection = Record<string, any>;
/* 当前的顶层窗口 */
const topWin: any = window.top;

/* 当前窗口是否在顶层（不在iframe里面） */
export const isThisTopWindow = window === topWin;
/* 当前是否在钉钉容器中 */
let nowInDingTalk = (navigator.appVersion || '').includes('DingTalk');
/* 当前是否在钉钉容器中 */
export const isDingTalk = () => nowInDingTalk;

/* 父子窗口约定在window上存放jsapi的地方 */
const FIELD = 'dd_jspi';
/* 顶层窗口的api集合 */
let JsApiFromTop: APICollection = {
  isDingTalk,
};
try {
  JsApiFromTop = topWin[FIELD] || {};
  topWin[FIELD] = JsApiFromTop;
} catch (e) {
  console.warn(e);
}

/**
 * 初始化jsapi集合，通过包装能做到
 * - 如果是在iframe中，就从顶层窗口去取api
 * - 保留api原来的类型
 */
export default function initJsApi<M extends APICollection>(collection: M) {
  if (isThisTopWindow) {
    const { platform } = getENV();
    nowInDingTalk = nowInDingTalk || platform !== ENV_ENUM.notInDingTalk;
    /* 把api集合与已有的集合做合并（非覆盖） */
    Object.assign(JsApiFromTop, collection);
  } else {
    const judger = JsApiFromTop.isDingTalk;
    if (typeof judger === 'function') {
      nowInDingTalk = judger();
    }
  }

  type IOpts = {
    name?: string; // jsapi name
    onFail?: (err: Error) => void;
  };
  /* 默认兜底空api */
  const fallback: any = (opts: IOpts = {}) => {
    const { onFail, name } = opts;
    const err = new Error(`未找到对应的jsapi ${name}`);
    if (typeof onFail === 'function') onFail(err);
    return Promise.reject(err);
  };

  /* 提取一个api（保留了api原来的类型） */
  function api<K extends keyof M>(name: K) {
    const target = JsApiFromTop[name as string];
    if (target) return target as M[K];

    // Todo 判断如果容器能在ifame成功接通jsbridge，则依然返回 collection[name];
    return () => fallback({ name });
  }

  return api;
}
