const b64ToUint6 = (nChr: number) => {
  let result = 0;
  if (nChr > 64 && nChr < 91) {
    result = nChr - 65;
  } else if (nChr > 96 && nChr < 123) {
    result = nChr - 71;
  } else if (nChr > 47 && nChr < 58) {
    result = nChr + 4;
  } else if (nChr === 43) {
    result = 62;
  } else if (nChr === 47) {
    result = 63;
  } else {
    result = 0;
  }
  return result;
};

const uint6ToB64 = (nUint6: number) => {
  let result = 0;
  if (nUint6 < 26) {
    result = nUint6 + 65;
  } else if (nUint6 < 52) {
    result = nUint6 + 71;
  } else if (nUint6 < 62) {
    result = nUint6 - 4;
  } else if (nUint6 === 62) {
    result = 43;
  } else if (nUint6 === 63) {
    result = 47;
  } else {
    result = 65;
  }
  return result;
};

export const strToUTF8Arr = (sDOMStr: string) => {
  const nStrLen = sDOMStr.length;
  // nChr 8位的字节数
  let nArrLen = 0;
  let nChr = 0;
  /* mapping... */
  for (let nMapIdx = 0; nMapIdx < nStrLen; nMapIdx++) {
    nChr = sDOMStr.charCodeAt(nMapIdx);
    if (nChr < 0x80) {
      nArrLen += 1;
    } else if (nChr < 0x800) {
      nArrLen += 2;
    } else if (nChr < 0x10000) {
      nArrLen += 3;
    } else if (nChr < 0x200000) {
      nArrLen += 4;
    } else if (nChr < 0x4000000) {
      nArrLen += 5;
    } else {
      nArrLen += 6;
    }
  }

  // 初始化字节数组
  const aBytes = new Uint8Array(nArrLen);
  // 将JS的字符串转换成UTF-8的字节数组
  for (let nIdx = 0, nChrIdx = 0; nIdx < nArrLen; nChrIdx++) {
    nChr = sDOMStr.charCodeAt(nChrIdx);
    if (nChr < 128) {
      /* one byte */
      aBytes[nIdx++] = nChr;
    } else if (nChr < 0x800) {
      /* two bytes */
      aBytes[nIdx++] = 192 + (nChr >>> 6);
      aBytes[nIdx++] = 128 + (nChr & 63);
    } else if (nChr < 0x10000) {
      /* three bytes */
      aBytes[nIdx++] = 224 + (nChr >>> 12);
      aBytes[nIdx++] = 128 + ((nChr >>> 6) & 63);
      aBytes[nIdx++] = 128 + (nChr & 63);
    } else if (nChr < 0x200000) {
      /* four bytes */
      aBytes[nIdx++] = 240 + (nChr >>> 18);
      aBytes[nIdx++] = 128 + ((nChr >>> 12) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 6) & 63);
      aBytes[nIdx++] = 128 + (nChr & 63);
    } else if (nChr < 0x4000000) {
      /* five bytes */
      aBytes[nIdx++] = 248 + (nChr >>> 24);
      aBytes[nIdx++] = 128 + ((nChr >>> 18) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 12) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 6) & 63);
      aBytes[nIdx++] = 128 + (nChr & 63);
    } else /* if (nChr <= 0x7fffffff) */ {
      /* six bytes */
      aBytes[nIdx++] = 252 + (nChr >>> 30);
      aBytes[nIdx++] = 128 + ((nChr >>> 24) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 18) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 12) & 63);
      aBytes[nIdx++] = 128 + ((nChr >>> 6) & 63);
      aBytes[nIdx++] = 128 + (nChr & 63);
    }
  }

  return aBytes;
};

export const base64EncArr = (aBytes: Uint8Array) => {
  const eqLen = (3 - (aBytes.length % 3)) % 3;
  let sB64Enc = '';

  // base64编码
  for (let nMod3, nLen = aBytes.length, nUint24 = 0, nIdx = 0; nIdx < nLen; nIdx++) {
    nMod3 = nIdx % 3;
    /* Uncomment the following line in order to split the output in lines 76-character long: */
    /*
    if (nIdx > 0 && (nIdx * 4 / 3) % 76 === 0) { sB64Enc += "\r\n"; }
    */
    nUint24 |= aBytes[nIdx] << ((16 >>> nMod3) & 24);
    if (nMod3 === 2 || aBytes.length - nIdx === 1) {
      sB64Enc += String.fromCharCode(uint6ToB64((nUint24 >>> 18) & 63), uint6ToB64((nUint24 >>> 12) & 63), uint6ToB64((nUint24 >>> 6) & 63), uint6ToB64(nUint24 & 63));
      nUint24 = 0;
    }
  }

  return eqLen === 0
    ? sB64Enc
    : sB64Enc.substring(0, sB64Enc.length - eqLen) + (eqLen === 1 ? '=' : '==');
};

// base64解码成utf-8字符串
export const base64DecToArr = (sBase64: string, nBlockSize?: number) => {
  // 过滤无效字符
  const sB64Enc = sBase64.replace(/[^A-Za-z0-9+/]/g, '');
  const nInLen = sB64Enc.length;
  // 初始化输出的utf-8数组
  const nOutLen = nBlockSize ? Math.ceil((nInLen * 3 + 1 >>> 2) / nBlockSize) * nBlockSize : nInLen * 3 + 1 >>> 2;
  const aBytes = new Uint8Array(nOutLen);

  // base64解析成utf-8数组
  for (let nMod3, nMod4, nUint24 = 0, nOutIdx = 0, nInIdx = 0; nInIdx < nInLen; nInIdx++) {
    nMod4 = nInIdx & 3;
    nUint24 |= b64ToUint6(sB64Enc.charCodeAt(nInIdx)) << 18 - 6 * nMod4;
    if (nMod4 === 3 || nInLen - nInIdx === 1) {
      for (nMod3 = 0; nMod3 < 3 && nOutIdx < nOutLen; nMod3++, nOutIdx++) {
        aBytes[nOutIdx] = (nUint24 >>> ((16 >>> nMod3) & 24)) & 255;
      }
      nUint24 = 0;
    }
  }

  return aBytes;
};

// utf-8字符数组转换成JS字符串
export const UTF8ArrToStr = (aBytes: Uint8Array) => {
  const nLen = aBytes.length;
  let sView = '';
  for (let nPart, nIdx = 0; nIdx < nLen; nIdx++) {
    nPart = aBytes[nIdx];
    let chr = 0;
    if (nPart > 251 && nPart < 254 && nIdx + 5 < nLen) { /* six bytes */
      /* (nPart - 252 << 30) may be not so safe in ECMAScript! So...: */
      chr = (nPart - 252) * 1073741824 + (aBytes[++nIdx] - 128 << 24) + (aBytes[++nIdx] - 128 << 18) + (aBytes[++nIdx] - 128 << 12) + (aBytes[++nIdx] - 128 << 6) + aBytes[++nIdx] - 128;
    } else if (nPart > 247 && nPart < 252 && nIdx + 4 < nLen) { /* five bytes */
      chr = (nPart - 248 << 24) + (aBytes[++nIdx] - 128 << 18) + (aBytes[++nIdx] - 128 << 12) + (aBytes[++nIdx] - 128 << 6) + aBytes[++nIdx] - 128;
    } else if (nPart > 239 && nPart < 248 && nIdx + 3 < nLen) { /* four bytes */
      chr = (nPart - 240 << 18) + (aBytes[++nIdx] - 128 << 12) + (aBytes[++nIdx] - 128 << 6) + aBytes[++nIdx] - 128;
    } else if (nPart > 223 && nPart < 240 && nIdx + 2 < nLen) { /* three bytes */
      chr = (nPart - 224 << 12) + (aBytes[++nIdx] - 128 << 6) + aBytes[++nIdx] - 128;
    } else if (nPart > 191 && nPart < 224 && nIdx + 1 < nLen) { /* two bytes */
      chr = (nPart - 192 << 6) + aBytes[++nIdx] - 128;
    } else { /* nPart < 127 ? */ /* one byte */
      chr = nPart;
    }
    sView += String.fromCharCode(chr);
  }

  return sView;
};

// UTF-8的字符串转Base64编码
export const textToBase64 = (text: string) => {
  let encode = '';
  try {
    encode = base64EncArr(strToUTF8Arr(text));
  } catch (err) {
    console.error('textToBase64 error:', err);
  }
  return encode;
};

// Base64编码转UTF-8的字符串
export const base64ToText = (data: string) => {
  let decode = '';
  try {
    decode = UTF8ArrToStr(base64DecToArr(data));
  } catch (err) {
    console.error('textToBase64 error:', err);
  }
  return decode;
};
