import { useRef } from 'react';

type PromiseType = { promise: Promise<number>; cancel: () => void };

const noop = () => {};
const delay = (n: number): Promise<number> => new Promise((resolve) => setTimeout(resolve, n));

const cancellablePromise = (promise: Promise<number>): PromiseType => {
  let isCanceled = false;
  const wrappedPromise: Promise<number> = new Promise((resolve, reject): void => {
    promise.then(
      (value) => (isCanceled ? reject({ isCanceled, value }) : resolve(value)),
      (error) => reject({ isCanceled, error }),
    );
  });
  return {
    promise: wrappedPromise,
    cancel: () => (isCanceled = true),
  };
};

const useCancellablePromises = () => {
  const pendingPromises = useRef<PromiseType[]>([]);

  const appendPendingPromise = (promise: PromiseType) => (pendingPromises.current = [...pendingPromises.current, promise]);

  const removePendingPromise = (promise: PromiseType) => (pendingPromises.current = pendingPromises.current.filter((p) => p !== promise));

  const clearPendingPromises = () => pendingPromises.current.map((p) => p.cancel());

  const api = {
    appendPendingPromise,
    removePendingPromise,
    clearPendingPromises,
  };
  return api;
};

export const useClickPreventionOnDoubleClick = (
  onClick?: (e: React.MouseEvent<HTMLElement>) => void,
  onDoubleClick?: (e: React.MouseEvent<HTMLElement>) => void,
) => {
  if (!onDoubleClick) return [onClick || noop, noop];
  const api = useCancellablePromises();
  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    api.clearPendingPromises();
    const waitForClick = cancellablePromise(delay(300));
    api.appendPendingPromise(waitForClick);

    return waitForClick.promise
      .then(() => {
        api.removePendingPromise(waitForClick);
        onClick && onClick(e);
      })
      .catch((errorInfo) => {
        api.removePendingPromise(waitForClick);
        if (!errorInfo.isCanceled) {
          throw errorInfo.error;
        }
      });
  };

  const handleDoubleClick = (e: React.MouseEvent<HTMLElement>) => {
    api.clearPendingPromises();
    onDoubleClick(e);
  };

  return [handleClick, handleDoubleClick];
};
