import React, { useContext } from 'react';
import dayjs from 'dayjs';
import localeData from 'dayjs/plugin/localeData';
import i18next from 'i18next';

dayjs.extend(localeData);

const resources: Record<string, { translation: Record<string, string> }> = {};

const defaultLocale = 'zh-CN';

const LocaleContext = React.createContext(defaultLocale);

export const LocaleProvider = LocaleContext.Provider;

export function defineLocale(lang: string, res: Record<string, string>): void {
  resources[lang] = {
    translation: {
      ...(resources[lang] ? resources[lang].translation : {}),
      ...res,
    },
  };
}

export function useTranslate(locale: string | undefined): any {
  const contextLocale = useContext(LocaleContext);
  const l = locale || contextLocale || defaultLocale;
  return getT(l);
}

export function useGetContextLocale() {
  return useContext(LocaleContext);
}

const cacheT: Record<string, any> = {};

function getT(locale: string) {
  if (cacheT[locale]) {
    return cacheT[locale];
  }

  const inst = i18next.createInstance();
  inst.init({
    fallbackLng: defaultLocale,
    lng: locale,
    resources,
  });
  cacheT[locale] = inst.t.bind(inst);
  return cacheT[locale];
}

export function useDayjsLocale(locale?: string): any {
  const contextLocale = useContext(LocaleContext);
  const l = locale || contextLocale || defaultLocale;
  const DAYJS_LOCALE_MAP: { [key: string]: string } = {
    'zh-CN': 'zh-cn',
    zh_CN: 'zh-cn',
    'zh-TW': 'zh-tw',
    zh_TW: 'zh-tw',
    'zh-HK': 'zh-hk',
    zh_HK: 'zh-hk',
    ja: 'ja',
    ja_JP: 'ja',
    'ja-JP': 'ja',
    'en-US': 'en',
    en_US: 'en',
  };
  return DAYJS_LOCALE_MAP[l];
}

interface ITranslateProps {
  locale?: string
  textKey: string
}

/** 用于class组件 */
export const TranslateComp: React.FC<ITranslateProps> = React.memo((props) => {
  const { locale, textKey } = props;
  const t = useTranslate(locale);
  return t(textKey);
});
