import styled from 'styled-components';
import React, { useEffect, useRef, useState } from 'react';
import classnames from 'classnames';

const SHOW_TIME = 100;
const ORG_SCALE = 0.8;
interface DropdownAnimationProps {
  children: React.ReactNode;
  visible: boolean;
  maxHeight?: number | string;
  overlayStyle?: React.CSSProperties;
  onClick?: () => void;
  placement?:
  | 'top'
  | 'left'
  | 'right'
  | 'bottom'
  | 'topLeft'
  | 'topRight'
  | 'bottomLeft'
  | 'bottomRight'
  | 'leftTop'
  | 'leftBottom'
  | 'rightTop'
  | 'rightBottom';
  className?: string;
}

export const DropdownAnimation: React.FC<DropdownAnimationProps> = React.memo(
  (props) => {
    const {
      children,
      visible,
      maxHeight,
      overlayStyle,
      placement,
      className,
      ...reps
    } = props;
    const [show, setShow] = useState(false);
    const [hide, setHide] = useState(false);
    const el = useRef<HTMLElement>(null);

    const parentEl: HTMLElement | undefined | null = el.current?.closest(
      '.popup-overlay-wrap',
    );

    if (parentEl) {
      parentEl.style.padding = '0';
      parentEl.style.border = 'none';
      parentEl.style.boxShadow = 'none';
      parentEl.style.backgroundColor = 'transparent';
    }

    useEffect(() => {
      if (el.current && maxHeight) {
        el.current.style.overflow = 'hidden';
      }
      setShow(visible);
      const timer = setTimeout(() => {
        if (visible) setShow(false);
        if (el.current) {
          el.current.style.boxShadow =
            '0px 0px 1px rgba(0,0,0,0.24), 0px 8px 24px rgba(10,30,65,0.16)';
          if (maxHeight) {
            el.current.style.overflow = 'auto';
          }
        }
      }, SHOW_TIME);
      return () => {
        clearTimeout(timer);
        setHide(true);
      };
    }, [el, visible, maxHeight]);

    const placementClassName = () => {
      if (!el.current) return '';
      if (placement === 'topLeft') {
        const popup = el.current.closest('.popup-overlay-wrap');
        if (popup) {
          const position = popup.getAttribute('position') || 'tl,bl';
          if (position === 'bl,tl') return 'bottomLeft';
          if (position === 'tl,bl') return 'topLeft';
          if (position === 'bc,tc') return 'bottom';
          if (position === 'tc,bc') return 'top';
          if (position === 'tr,br') return 'topRight';
          if (position === 'br,tr') return 'bottomRight';
          if (position === 'cr,cl') return 'right';
          if (position === 'tr,tl') return 'rightTop';
          if (position === 'tl,tr') return 'leftTop';
          if (position === 'bl,br') return 'leftBottom';
          if (position === 'cl,cr') return 'left';
          if (position === 'br,bl') return 'rightBottom';
          return 'topLeft';
        }
      } else {
        return placement;
      }
    };

    return (
      <Overlay
        ref={el}
        style={{ ...overlayStyle, maxHeight }}
        className={classnames({ show, hide }, className, placementClassName())}
        {...reps}
      >
        {children}
      </Overlay>
    );
  },
);

DropdownAnimation.defaultProps = {
  placement: 'topLeft',
};

export const DropdownAnimationContext = React.createContext<string[]>([]);

const Overlay: React.ComponentType<any> = styled.div`
  width: 100%;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.24),
    0px 8px 24px rgba(10, 30, 65, 0.16);
  border-radius: 4px;
  background-color: #fff;
  &.show {
    &.top {
      animation: ${'top'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.left {
      animation: ${'left'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.right {
      animation: ${'right'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.bottom {
      animation: ${'bottom'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.topLeft {
      animation: ${'topLeft'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.topRight {
      animation: ${'topRight'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
    &.bottomLeft {
      animation: ${'bottomLeft'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
    &.bottomRight {
      animation: ${'bottomRight'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
    &.leftTop {
      animation: ${'leftTop'} ${SHOW_TIME}ms cubic-bezier(0.39, 0.575, 0.565, 1)
        forwards;
    }
    &.leftBottom {
      animation: ${'leftBottom'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
    &.rightTop {
      animation: ${'rightTop'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
    &.rightBottom {
      animation: ${'rightBottom'} ${SHOW_TIME}ms
        cubic-bezier(0.39, 0.575, 0.565, 1) forwards;
    }
  }

  @keyframes topLeft {
    0% {
      transform: scale(${ORG_SCALE});
      opacity: 0;
      transform-origin: 0% 0%;
    }
    100% {
      transform: scale(1);
      opacity: 1;
      transform-origin: 0% 0%;
    }
  }
  @keyframes leftTop {
    0% {
      transform: scale(${ORG_SCALE});
      opacity: 0;
      transform-origin: 0% 0%;
    }
    100% {
      transform: scale(1);
      opacity: 1;
      transform-origin: 0% 0%;
    }
  }
  @keyframes top {
    0% {
      transform: translateY(-12px);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  @keyframes topRight {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 100% 0%;
    }
    100% {
      transform: scale(1);
      transform-origin: 100% 0%;
    }
  }
  @keyframes rightTop {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 100% 0%;
    }
    100% {
      transform: scale(1);
      transform-origin: 100% 0%;
    }
  }
  @keyframes right {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 100% 50%;
    }
    100% {
      transform: scale(1);
      transform-origin: 100% 50%;
    }
  }
  @keyframes rightBottom {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 100% 100%;
    }
    100% {
      transform: scale(1);
      transform-origin: 100% 100%;
    }
  }
  @keyframes bottomRight {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 100% 100%;
    }
    100% {
      transform: scale(1);
      transform-origin: 100% 100%;
    }
  }
  @keyframes bottom {
    0% {
      transform: translateY(12px);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  @keyframes bottomLeft {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 0% 100%;
    }
    100% {
      transform: scale(1);
      transform-origin: 0% 100%;
    }
  }
  @keyframes leftBottom {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 0% 100%;
    }
    100% {
      transform: scale(1);
      transform-origin: 0% 100%;
    }
  }
  @keyframes left {
    0% {
      transform: scale(${ORG_SCALE});
      transform-origin: 0% 50%;
    }
    100% {
      transform: scale(1);
      transform-origin: 0% 50%;
    }
  }
`;
